import React from "react";
import "normalize.css/normalize.css";
import { createGlobalStyle } from "styled-components";
import { ThemeProvider, Styled } from "theme-ui";
import Monument from "./fonts/MonumentExtended-Ultrabold.woff";
import Monument2 from "./fonts/MonumentExtended-Ultrabold.woff2";
import MonumentOTF from "./fonts/MonumentExtended-Ultrabold.otf";
import MonumentTTF from "./fonts/MonumentExtended-Ultrabold.ttf";
import MonumentR from "./fonts/MonumentExtended-Regular.woff";
import MonumentR2 from "./fonts/MonumentExtended-Regular.woff2";
import MonumentROTF from "./fonts/MonumentExtended-Regular.otf";
import MonumentRTTF from "./fonts/MonumentExtended-Regular.ttf";
import Akz from "./fonts/AkzidenzGrotesk-Regular.woff";
import Akz2 from "./fonts/AkzidenzGrotesk-Regular.woff2";
import AkzOTF from "./fonts/AkzidenzGrotesk-Regular.otf";
import AkzTTF from "./fonts/AkzidenzGrotesk-Regular.ttf";

import theme from "@src/theme";
export const GlobalStyle = createGlobalStyle`



@font-face {
  font-family: "Monument";
src: url(${Monument}) format("woff"),
    url(${Monument2}) format("woff2"),
    url(${MonumentOTF}) format("opentype"),
    url(${MonumentTTF}) format("truetype");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Monument";
src: url(${MonumentR}) format("woff"),
    url(${MonumentR2}) format("woff2"),
    url(${MonumentROTF}) format("opentype"),
    url(${MonumentRTTF}) format("truetype");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Akz";
src: url(${Akz}) format("woff"),
    url(${Akz2}) format("woff2"),
    url(${AkzOTF}) format("opentype"),
    url(${AkzTTF}) format("truetype");
  font-style: normal;
  font-weight: 400;
}


  * {
    box-sizing: border-box;

  }

  html {
    -webkit-print-color-adjust: exact;
    font-family: "Akz";
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.5;
    letter-spacing: 0.25px;
    font-size: 18px;
    position: relative;
    color: ${theme.colors.coal};
  }


  body {
    min-height: 100%;
  }

  a:hover {
    text-decoration: none;
  }


`;

export default ({ element }) => (
  <React.Fragment>
    <ThemeProvider theme={theme}>
      <GlobalStyle />

      <Styled.root>{element}</Styled.root>
    </ThemeProvider>
  </React.Fragment>
);
